<template>
    <el-dialog title="添加角色" :visible.sync="isShow" width="80%" :before-close="handleClose">
        <!-- 角色表单 -->
        <el-form :model="botData" :inline="true" label-position="top">
            <el-form-item label="角色名称" :label-width="formLabelWidth">
                <el-input v-model="botData.botName" autocomplete="off" placeholder="请输入角色名称" clearable
                    size="medium"></el-input>
            </el-form-item>
            <el-form-item label="角色故事">
                <el-input v-model="botData.botDesc" type="textarea" :autosize="{ minRows: 2, maxRows: 4 }"
                    placeholder="请输入…" resize="none" maxlength="100" show-word-limit>
                </el-input>
            </el-form-item>
            <el-form-item label="角色头像">
                <!-- 饿了么上传接口有跨域问题 -->
                <!-- <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" -->
                <el-upload class="avatar-uploader" action=""
                    :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
                    <!-- <img v-if="botData.botAvatar" :src="botData.botAvatar" class="avatar"> -->
                    <el-avatar v-if="botData.botAvatar" :src="botData.botAvatar" class="avatar"></el-avatar>
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <!-- <el-form-item label="prompt" :label-width="formLabelWidth">
                <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="职业" :label-width="formLabelWidth">
                <el-select v-model="form.region" placeholder="请选择职业">
                    <el-option label="老师" value="shanghai"></el-option>
                    <el-option label="医生" value="beijing"></el-option>
                </el-select>
            </el-form-item> -->
        </el-form>
        <!-- 提示词表单 -->
        <persona-prompt @savePrompts="handleFormSubmit"/>
        <!-- 底部按钮 -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="handleClose">取 消</el-button>
            <el-button type="primary" @click="handleSubmit">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
import PersonaPrompt from './PersonaPrompt.vue';

export default {
  components: { PersonaPrompt },
    props: ['showAddCharacterDialog'],
    data() {
        return {
            //默认关闭添加角色dialog
            // showAddCharacterDialog: false,
            botData: {
                botName: 'xxxxxx',
                botDesc: 'bbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbb',
                botAvatar: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
                prompt: '',
            },
            // input大小
            formLabelWidth: '380px',
            // dialog是否展示
            isShow: false,
        }
    },
    watch: {
        // 监听父组件纯的showAddCharacterDialog，赋值
        showAddCharacterDialog: {
            handler(val) {
                this.isShow = val
            }
        }
    },
    mounted() {

    },
    computed: {

    },
    methods: {
        // 处理prompts表单
        handleFormSubmit(formData) {
            // 处理子组件提交的表单数据
            console.log(formData);
            this.botData.prompt = JSON.stringify(formData);
            // 发送到服务器的逻辑
        },
        // 传给父组件，通知其关闭dialog
        toCloseDialog() {
            this.$emit('dialog-cancel');
        },
        // 关闭dialog前的回调
        handleClose() {
            // 关闭表单
            this.toCloseDialog()
            // 消息提示
            this.$message({
                message: '警告，确认关闭',
                type: 'warning'
            });
        },
        // 提交表单
        handleSubmit() {
            this.$confirm('确认提交？')
                .then(_ => {
                    let userId = window.localStorage.getItem('userId')
                    this.botData.userId = userId
                    // 请求添加
                    this.$httpPost('CreateUserBot', this.botData, true, userId, 'json')
                        .then(res => {
                            // 根据响应判断
                            if (res.data.code === 0) {
                                this.$message({
                                    message: '角色添加成功！',
                                    type: 'success'
                                });
                                this.botData = {}
                            }else{
                                this.$message({
                                    message: res.msg,
                                    type: 'error'
                                });
                            }
                            // 关闭表单
                            this.toCloseDialog()
                        }).catch(err => {
                            console.log(err)
                        })
                })
                .catch(_ => { });
        },
        // 上传头像前检查
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        // 上传头像成功回调
        handleAvatarSuccess(res, file) {
            this.botData.botAvatar = URL.createObjectURL(file.raw);
        },
    },
}
</script>
<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
</style>
