import request from './request';
import { HTTP_URI } from './api';

export const REQUEST = {
  POST: function (businessType, data, isToken, pathParams = '',requestType = 'json') {
    if (!HTTP_URI[businessType]) return;

    const headers = {};

    // 如果需要进行 JWT 鉴权，则添加 Authorization 头部
    if (isToken) {
      const jwtToken = window.localStorage.getItem('token'); // 从 localStorage 中获取 token
      if (jwtToken) {
        headers['Authorization'] = `Bearer ${jwtToken}`;
      } else {
        console.log('不存在token, 未登录');
      }
    }

    let url = HTTP_URI[businessType]
    if (typeof url === 'function') {
      url = url(pathParams)
    }

    if (requestType === 'json') {
      headers['Content-Type'] = 'application/json';

      return request({
        url: url,
        method: 'post',
        headers: headers,
        data: JSON.stringify(data),
      });
    } else if (requestType === 'query') {
      headers['Content-Type'] = 'application/x-www-form-urlencoded';

      // 根据业务需求，动态构建查询参数字符串
      const queryParams = Object.entries(data)
        .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
        .join('&');

      return request({
        url: `${url}?${queryParams}`,
        method: 'post',
        headers: headers,
      });
    }
  },

  GET: function (businessType, params, isToken, pathParams = '',requestType = 'json') {
    if (!HTTP_URI[businessType]) return;
    const headers = {};

    // 如果需要进行 JWT 鉴权，则添加 Authorization 头部
    if (isToken) {
      const jwtToken = window.localStorage.getItem('token'); // 从 localStorage 中获取 token
      if (jwtToken) {
        headers['Authorization'] = `Bearer ${jwtToken}`;
      } else {
        console.log('不存在token, 未登录');
      }
    }

    // 动态生成url？
    let url = HTTP_URI[businessType]
    if (typeof url === 'function') {
      url = url(pathParams)
    }

    // console.log('url:%s',url)

    // 请求格式
    if (requestType === 'json') {
      headers['Content-Type'] = 'application/json';

      return request({
        url: url,
        method: 'get',
        headers: {
          ...headers,
          'Content-Type': 'application/json',
          'X-Request-Params': JSON.stringify(params)
        },
        // data: JSON.stringify(params),
      });
    } else if (requestType === 'query') {
      headers['Content-Type'] = 'application/x-www-form-urlencoded';

      // 根据业务需求，动态构建查询参数字符串
      const queryParams = Object.entries(params)
        .map(([key, value]) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`)
        .join('&');

      return request({
        url: `${url}?${queryParams}`,
        method: 'get',
        headers: headers,
      });
    }
  },
};

export default REQUEST;


