<template>
    <el-row class="nav-bar">
      <!-- 左侧logo -->
    <el-col :span="5">
      <div class="logo">
        <img src="@/assets/images/logo.png" alt="Logo" @click="toHome">
      </div>
    </el-col>

    <!-- 中部 -->
    <el-col :span="3" :offset="3">
      <div class="nav selected_nav">
        <img :src="collect_icon" alt="collect_icon">
        <p>典藏</p>
      </div>
    </el-col>
    <el-col :span="3">
      <div class="nav unselected_nav">
        <img :src="auction_icon" alt="auction_icon">
        <p>拍卖行</p>
      </div>
    </el-col>
    <!-- 右侧搜索框 -->
    <el-col :span="6" :offset="4">
      <div class="search-box">
        <el-input
          placeholder="Search"
          suffix-icon="el-icon-search"
          v-model="searchText"
          clearable
          type="search"
          @change="onSearchChange" size="medium">
        </el-input>
      </div>
    </el-col>
    </el-row>
  </template>
  <script>
  import auction_selected from '../assets/images/auction_selected.png'
  import auction_unselected from '../assets/images/auction_unselected.png'
  import collect_selected from '../assets/images/collect_selected.png'
  import collect_unselected from '../assets/images/collect_unselected.png'

  export default {
    data() {
      return {
          searchText: '',  // 搜索关键词
          auction_icon: auction_unselected,
          collect_icon: collect_selected,
          query: {
            //查询参数
            botName: "", //allow null
            botDesc: "", //allow null
            pageNum: 1, //*
          }
        }
    },
    methods: {
      // 跳转首页
      toHome() {
        this.$router.push('/')
      },
      onSearchChange() {
      //   this.keyword = val  // 绑定搜索框值到keyword
          console.log(this.searchText)
      },
      search(keyword) {
        const userId = window.localStorage.getItem('userId');
        //构建用户URL
        // 发起搜索请求,keyword 为搜索关键词
        this.$httpGet('GetUserBot',this.query,true,userId,'query').then(res => {
            // 搜索成功,处理结果
            let resData = res.data;
            console.log(resData)
          })
          .catch(err => {
            // 搜索失败,输出错误
            console.log(err)
          })
      }
    },
    watch: {
      keyword(newVal) {
        if (newVal) {  // 只有当关键词不为空时发起搜索请求
          this.search(newVal)
        }
      }
    },

  }
  </script>

