<template>
  <div class="s">
    <div class="login">
      <div class="logo">
        <img :src="require('../assets/images/icon.jpg')" style="width:60px;height:60px">
      </div>
      <h2 v-if="showPage">欢迎登录
      </h2>
      <h2 v-if="!showPage">
        用户注册
      </h2>
      <el-card class="forms" v-if="showPage" shadow="never">

        <el-form :model="form" :rules="rules" ref="ruleForm" label-position="top" label-width="0px" class="demo-ruleForm">
          <el-form-item prop="username">
            <el-input prefix-icon="el-icon-mobile-phone" placeholder="请输入用户名" v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input :type="type" prefix-icon="el-icon-s-goods" placeholder="请输入密码" v-model="form.password">
              <i slot="suffix" @click="type == 'password' ? type = 'text' : type = 'password'"
                class="el-input__icon el-icon-view"></i>
            </el-input>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
            <el-button type="primary" @click="login" style="width:100%">登录</el-button>
          </el-form-item>
          <el-form-item style="margin-bottom: 0;">
          </el-form-item>
        </el-form>
      </el-card>
      <el-card class="forms" shadow="never" v-if="!showPage">

        <accountVue @reglogin="reglogin" v-if="regs == 1">
        </accountVue>
      </el-card>
      <el-button v-if="showPage" @click="reg" style="width:350px;margin-top:30px">还没有账号？立即注册</el-button>
      <el-button v-if="!showPage" @click="reg" style="width:350px;margin-top:30px">已有账号，立即登录</el-button>
    </div>
  </div>
</template>

<script>
import accountVue from './components/account.vue'
import md5 from 'md5'

export default {
  name: "LoginView",
  components: { accountVue },
  data() {
    return {
      showPage: true,
      form: {
        username: '',
        password: '',
      },
      disabled: false,
      time: '',
      type: 'password',
      regs: 0,
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'change' }],
        password: [{ required: true, message: '请输入密码', trigger: 'change' }],
      }
    }
  },
  methods: {
    // 获取名为"jwt"的Cookie值
    getJwtToken() {
      const cookies = document.cookie.split(';');
      for (let i = 0; i < cookies.length; i++) {
        const cookie = cookies[i].trim();
        if (cookie.startsWith('jwt=')) {
          return cookie.substring(4);
        }
      }
      return null;
    },
    //获取Set-Cookie中的jwt token
    getJwtTokenBySetCookie(response) {
      try {
        // 获取响应头部中的"jwt" Cookie的值
        //由于浏览器跨域问题只能使用x-set-cookie代替
        var jwtCookie = ''
        if (response.headers.get('x-set-cookie')) {
          jwtCookie = response.headers['x-set-cookie'];
        }
        if (response.headers.get('set-cookie')) {
          jwtCookie = response.headers['set-cookie'];
        }
        // const jwtCookie = response.headers['x-set-cookie'];
        console.log(jwtCookie);
        if (jwtCookie == '') {
          console.log("获取不到cookie")
          return null;
        }
        const tokenRegex = /jwt=([^;]+)/;
        // const jwtToken = jwtCookie.split(';')[0].split('=')[1];
        const jwtToken = jwtCookie.match(tokenRegex)[1];
        if (jwtToken) {
          return jwtToken;
        }
        return null;
      } catch (error) {
        console.error('getJWTError: ', error);
        return null;
      }
    },
    login() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.form.password = md5(this.form.password);

          this.$httpPost('LOGIN', this.form, false, '', 'json').then(res => {
            let resData = res.data;
            console.log("Code: ", resData.code);
            if (resData.code != 0) {
              console.log(resData)
              this.$alert(resData.msg, '提示')
            } else {
              //获取jwt token进行存储
              // let token = this.getJwtToken();
              let token = this.getJwtTokenBySetCookie(res);
              if (token != null) {
                window.localStorage.setItem('userId', resData.data);
                window.localStorage.setItem('token', token)
              } else {
                console.log('token is null!')
                this.$message.success('登录失败！')
                return
              }
              // window.localStorage.setItem('userInfo', JSON.stringify(res.data))
              // window.localStorage.setItem('token', res.data.token)
              this.$message.success('登录成功！')
              if (this._isMobile()) {
                window.localStorage.setItem('phone', true)
              } else {
                window.localStorage.setItem('phone', false)
              }
              setTimeout(() => {
                this.$router.push('/')
              }, 2000)
            }
          })
        }
      })
    },
    reglogin(data) {
      this.$httpPost('REGISTER', data, false, '', 'json').then(res => {
        let resData = res.data;
        if (resData.code == 0) {
          this.$message.success('注册成功！')
          this.showPage = !this.showPage
        } else {
          this.$message.error(resData.message)
        }
      })
    },
    //   reglogins(data) {
    //     this.$https('CODEREG', data).then(res => {
    //       if (res.status == 200) {
    //         this.$message.success('注册成功！')
    //         this.showPage = !this.showPage
    //         this.$alert(res.data, '提示')
    //       } else {
    //         this.$message.error(res.msg)
    //       }
    //     })
    //   },
    //   regloginMail(data) {
    //     this.$https('EMAILREG', data).then(res => {
    //       if (res.status == 200) {
    //         this.$message.success('注册成功！')
    //         this.showPage = !this.showPage
    //         this.$alert(res.data, '提示')
    //       } else {
    //         this.$message.error(res.msg)
    //       }
    //     })
    //   },
    reg() {
      this.showPage = !this.showPage
      if (!this.showPage) {
        // this.$https('REGTYPE', {}).then(res => {
        //   this.regs = res.data
        // })
        this.regs = 1
      }
    },
    _isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      )
      return flag
    }
  }
}
</script>

