<template>
    <div class="persona-list">
        <div v-for="persona, index in personas" :key="index">
            <PersonaCard :persona="persona"  class="persona-card"/>
        </div>
    </div>
</template>

<script>
import PersonaCard from './PersonaCard.vue';
import logo from '../assets/images/1.png';

export default {
    name: 'PersonaList',
    components: {
        PersonaCard
    },
    data() {
        return {
            // 查询参数
            query: {
                pageNum: 1
            },
            // 角色数量
            personas: [
                // Add more personas as needed
            ]
        };
    },
    methods: {
        // 查询首页bot角色
        getBotHome() {
            this.$httpGet('GetBotsHome', this.query, true, '', 'query').then(res => {
                // 处理响应结果
                let resData = res.data;
                this.personas = resData.data.contentList
                console.log("首页：",res)
                //暂时本地先用静态图片代替
                this.personas.forEach(element => element.botAvatar = logo);
            }).catch(error => {
                // 处理错误
                console.log(error)
            });
        }
    },
    created() {
        // 查询首页bot角色
        this.getBotHome()
    }
}
</script>

<style scoped>
.persona-list {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
}

/* 在默认分辨率下一行展示五个小卡片 */
.persona-card {
    box-sizing:content-box;
