<template>
  <div class="sidebar">
    <!-- 个人信息导航栏 -->
    <div class="menu-item">
      <!-- 若未登录，无用户信息，显示登录导航 -->
      <div v-if="!user.avatar" @click="toLogin">
        <!-- <button>登录</button> -->
        <div class="circle login">
          <p>登录</p>
        </div>
      </div>
      <!-- 若已登录，有用户信息，显示用户信息，跳转至Profile组件（个人空间） -->
      <div v-else @click="toLogin">
        <!-- <div v-else @click="toProfile"> -->
        <img :src="user.avatar" class="circle">
      </div>
    </div>
    <!-- 主页导航栏 -->
    <div class="menu-item" @click="toHome">
      <div class="circle icon">
        <img :src="home_icon">
      </div>
      <p class="text">主页</p>
    </div>
    <!-- 聊天室导航栏 -->
    <div class="menu-item" @click="toSocket">
      <div class="circle icon">
        <img :src="chatroom_icon">
      </div>
      <p class="text">聊天室</p>
    </div>
    <!-- 创造导航栏 -->
    <div class="menu-item" @click="showAddCharacterDialog = true">
      <div class="circle icon">
        <img src="@/assets/images/create_unselected.png">
      </div>
      <p class="text">创造</p>
    </div>
    <!-- 添加角色弹框组件 -->
    <DialogAddCharacter :showAddCharacterDialog="showAddCharacterDialog"
                  @dialog-cancel="showAddCharacterDialog = false"></DialogAddCharacter>
  </div>
</template>

<script>
import avatar from "../assets/images/1.png"
import home_selected from '../assets/images/home_selected.png'
import home_unselected from '../assets/images/home_unselected.png'
import chatroom_selected from '../assets/images/chatroom_selected.png'
import chatroom_unselected from '../assets/images/chatroom_unselected.png'
import DialogAddCharacter from "./DialogAddCharacter.vue"

export default {
  components: { DialogAddCharacter },
  data() {
    return {
      // 用户对象
      user: {
        avatar: avatar,
        name: "test"
      },
      // 主页导航栏图片
      home_icon: home_unselected,
      // 聊天室导航栏图标
      chatroom_icon: chatroom_unselected,
      //默认关闭添加角色dialog
      showAddCharacterDialog: false,
    }
  },
  methods: {
    // 跳转登录
    toLogin() {
      this.$router.push('/login')
    },
    // 跳转主页
    toHome() {
      this.$router.push('/')
    },
    // 跳转聊天室
    toSocket() {
      this.$router.push('/chatroom/chat')
    },
    // 更改选中图标
    changeIcon() {
      let path = this.$route.path
      if (path === '/') {
        this.home_icon = home_selected
      } else if (path === '/chatroom/chat') {
        this.chatroom_icon = chatroom_selected
      }
    },
  },
  created() {
    // 更改选中图标
    this.changeIcon()
    // 获取已登录用户信息并赋值给user
  }
}
</script>

