<template>
    <el-form ref="form" @submit.native.prevent>
      <el-form-item label="提示词" prop="prompts">
        <el-input
          v-model="prompt"
          placeholder="请输入提示词"
          clearable
          @focus="showPlaceholder = false"
          @blur="resetPrompt"
          @change="handleInput"
        >
          <template slot="prepend">
            <span v-show="showPlaceholder" class="placeholder">示例: 提示词1,提示词2,提示词3</span>
          </template>
          <template slot="append">
            <el-button icon="el-icon-document-copy" @click="copyPrompts"></el-button>
            <el-button icon="el-icon-delete" @click="clearPrompts"></el-button>
          </template>
        </el-input>
      </el-form-item>
      <el-form-item label="标签展示">
        <div class="tags">
          <el-tag
            v-for="(tag, index) in tags"
            :key="index"
            :type="success"
            class="tag"
            @mouseenter="hoverTag(index, true)"
            @mouseleave="hoverTag(index, false)"
          >
            {{ tag }}
          </el-tag>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm">保存</el-button>
      </el-form-item>
    </el-form>
  </template>
  
  <script>
  export default {
    data() {
      return {
        prompt: '',
        showPlaceholder: true,
        tags: [
        //   { label: '标签1', type: 'success' },
        //   { label: '标签2', type: 'info' },
        //   { label: '标签3', type: 'warning' },
        //   { label: '标签4', type: 'danger' },
        ],
      };
    },
    methods: {
      handleInput() {
        console.log('input')
        if (this.prompt) {
            // 使用逗号分隔用户输入的内容，添加为一个新的标签
            const newTags = this.prompt.split(',').map(tag => tag.trim());
            // 过滤掉空的标签
            const filteredTags = newTags.filter(tag => tag !== '');
            // 将新的标签合并到现有的标签数组中
            this.tags = [...this.tags, ...filteredTags];
            // 清空输入框, 清空后继续输入
            this.prompt = '';
        }
      },
      //重置
      resetPrompt() {
        if (!this.prompt) {
          this.showPlaceholder = true;
        }
      },
      //复制
      copyPrompts() {
        // 复制提示词逻辑
      },
      //清楚
      clearPrompts() {
        // 清除提示词逻辑
      },
      //Tag的悬停特效
      hoverTag(index, isHover) {
        // 标签悬停动效逻辑
      },
      //保存提交到父组件
      submitForm() {
        // const formData = this.$refs.form.getFormData(); // 获取子表单数据
        // 提交逻辑
        this.$emit('savePrompts',this.tags)
      },
    },
  };
  </script>
  
  <style scoped>
  .placeholder {
    color: #c0c4cc;
  }
  
  .tags {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
  }
  
  .tag {
    display: inline-block;
    /* padding: 5px 10px; */
